import React from "react"
import styles from './Projects.module.css'
import ProjectItem from './ProjectItem';

const projects = [{
  "url": "https://github.com/HeyParkerJ/summit_melee_video_sort",
  "title": "melee_video_sort",
  "description": `Someone on Reddit said "Sorting them into folders could be possible but is a lot of work, unless someone is down to write a script for it!". Needing something to do while washing down my coffee - I wrote that script.`
}]

export default () => (
  <section className={styles.projects}>
    <ul className={styles.nav}>
      {projects.map((project) => {
        return <ProjectItem url={project.url} title={project.title} description={project.description} />
      })}
    </ul>
  </section>
)
